@import 'globals';

form.add-color {
	width: 98%;
	display: flex;

	> :first-child {
		flex-grow: 1;
	}

	input,
	button {
		margin: 0.5em;
		padding: 0.25em;
		font-size: 1.2em;
		border-radius: 5px;
	}

	input[type='color'] {
		padding: 0;
		height: +2em;
	}

	button {
		background-color: green;
		color: white;
		border: none;
	}
}
