@import 'globals';

div.star {
	cursor: pointer;
	height: 25px;
	width: 25px;
	margin: 2px;
	float: left;
	background-color: darken($light-grey, 40%);
	clip-path: polygon(
		50% 0%,
		63% 38%,
		100% 38%,
		69% 59%,
		82% 100%,
		50% 75%,
		18% 100%,
		31% 59%,
		0% 38%,
		37% 38%
	);

	&.selected {
		background-color: red;
	}
}
